*, *:before, *:after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

.header {
    background-image: url(img-game/header.png);
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 13px;

    .historical {
        padding-top: 20px;
        padding-bottom: 20px;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-around;
        background-color: #000000;
        opacity: 0.5;

        .historical-p {
            p.hist {
                margin: 0;
                color: #ffffff;
                font-family: Poppins;
                font-size: 36px;
                font-weight: 500;
                line-height: 22.36px;
                letter-spacing: 0.77px;
            }

            p.games {
                margin: 0;
                color: #ffffff;
                font-family: Poppins;
                font-size: 15px;
                font-weight: 500;
                line-height: 22.36px;
                text-transform: uppercase;
                letter-spacing: 31px;
            }
        }

        .historical-info {
            color: #ffffff;
            font-family: 'Roboto Condensed';
            font-size: 17px;
            font-weight: 400;
            line-height: 22.36px;
            letter-spacing: 0.36px;

            img {
                margin-right: 13px;
                margin-left: 40px;
            }
        }
    }

    .mainmenu-container {
        border-bottom: 1px solid #ffffff;
        display: flex;
        flex-direction: row;
        justify-content: center;

        ul.mainmenu {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            list-style: none;
            padding: 0;
            margin: 0 0 -2px 0;

            li a {
                display: inline-block;
                text-decoration: none;
                padding: 30px;
                color: #000000;
                font-family: 'Roboto Condensed';
                font-size: 17px;
                font-weight: 400;
                line-height: 22.36px;
            }

            li a.active {
                color: #ffffff;
                letter-spacing: 0.36px;
                border-bottom: 3px solid #ffffff;
            }

            li a:hover {
                color: #ffffff;
            }
        }
    }

    .header-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        align-content: center;


        .header-text {
            display: flex;
            flex-direction: column;
            align-items: center;

            .header-text-menu {
                margin-top: 135px;

                a {
                    text-decoration: none;
                    color: #ffffff;
                    font-family: 'Roboto Condensed';
                    font-size: 18px;
                    font-weight: 400;
                    line-height: 48px;
                    letter-spacing: 0.18px;
                    border-bottom: 3px solid #ffffff;
                    padding: 20px 0px;
                }
            }

            .header-text-p {
                margin-top: 75px;
                color: #ffffff;
                font-family: Poppins;
                font-size: 60px;
                font-weight: 500;
                line-height: 48px;
                letter-spacing: 0.6px;

                p {
                    margin: 0;
                }

            }

            .header-text-social {
                margin-top: 90px;
                margin-bottom: 155px;

                a {
                    display: inline-block;
                    text-align: center;
                    width: 45px;
                    height: 45px;
                    padding: 12px;
                    border: 1px solid #ffffff;
                    border-radius: 50%;
                    margin-right: 10px;
                }
            }
        }
    }
}

.about-us {

    .about-us-container {
        margin-top: 90px;
        display: flex;
        flex-direction: row;
        justify-content: center;

        .about-us-text {
            width: 400px;
            margin-right: 30px;

            h2 {
                margin: 0;
                color: #f44336;
                font-family: Poppins;
                font-size: 36px;
                font-weight: 500;
                line-height: 36px;
            }

            p{
                color: #999999;
                font-family: "Roboto Condensed";
                font-size: 12px;
                font-weight: 300;
                line-height: 24px;
                letter-spacing: 0.12px;
            }
        }

    }
}

.gallery{
    margin-top: 90px;
    margin-bottom: 90px;

    h2{
        margin: 0 0 92px 0;
        text-align: center;
        color: #f44336;
        font-family: Poppins;
        font-size: 36px;
        font-weight: 500;
        line-height: 48px;
    }

    .gallery-container{
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        
        .gallery-item{
            background-color: #f7f7f7;
            margin-right: 30px;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding-bottom: 13px;


            p{
                color: #000000;
                font-family: Poppins;
                font-size: 14px;
                font-weight: 500;
                line-height: 23.94px;
                text-transform: uppercase;
                letter-spacing: 0.14px;
                text-align: center;
            }

            button{
                width: 130px;
                height: 30px;
                background-color: #f44336;
                border: #f44336;
                color: #ffffff;
                font-family: Poppins;
                font-size: 14px;
                font-weight: 500;
                line-height: 24px;
                letter-spacing: 0.14px;
                margin-top: auto;
            }
        }
    }
}
